
import {useState} from "react"

export default function Card({ value }) {
    const [openColumn, setOpenColumn] = useState(false)
    return (
        <>
            <div className="m-4">
                <div className="rounded-3xl shadow-2xl h-20 w-full p-4  bg-gray-200 flex justify-center">
                    <div className="text-gray-900 rounded-xl bg-violet-300 h-12 w-12">{value}日</div>
                    <div className="h-12 w-52 bg-gray-400"><button onClick={() =>setOpenColumn(!openColumn)}>&lt;</button></div>
                </div>
                <div className="bg-gray-200" style={openColumn ? {maxHeight: "500px" } : {maxHeight: "0px", overflow: "hidden", transition: "maxHeight 2s ease"}}>
                    <p>今日の予定 fjjjjjjjjj<br />fjaojoajfa<br />fahfoaho</p>
                </div>
            </div>
        </>
    )
}